$color-base: #fff;
$color-accent: #00b6e3;

#navigation {
    position: relative;
    width: 100%; height: 50px;
    background: $color-base;
    box-shadow: 0 0 5px rgba(#000, 0.15);
    text-align: center;
    line-height: 50px;
    ul {
      display: flex;
      flex-flow: row wrap;
      justify-content: center;
      align-content: center;
      align-items: center;
      li {
        a {
          height: inherit;
          padding: 0 25px;
          display: inline-block;
          transition: all 150ms ease;
          line-height: inherit;
          text-transform: uppercase;
          &:hover {
            background: $color-accent;
            color: $color-base;
          }
        }
      }
    }
    &.fixed {
      position: fixed;
      top: 0; left: 0; right: 0;
    }
  }
  